@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --white: #fff;
  --black: #000;
}

::-webkit-input-placeholder{
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

:-ms-input-placeholder{
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

::placeholder{
  color: #8e8e8e;
  font-weight: 300;
  font-size: 12px;
}

body{
  font-family: 'Open Sans', sans-serif;
  background: #fafafa;
}

.navbar{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 54px;
  background: #fff;
  background: var(--white);
  display: -webkit-flex;
  display: flex;
  padding: 0 200px;
  border-bottom: 1px solid #efefef;
  -webkit-align-items: center;
          align-items: center;
}

.navbar__first{
  width: 33.33333333333333%;
}

.navbar__first-logo{
  margin-top: 6px;
}

.navbar__first-logo img{
  object-fit: cover;
  margin: 6px;
  display: block;
}

.navbar__middle{
  width: 33.33333333333333%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.navbar__middle-search{
  position: relative;
}

.navbar__search{
  background: #fafafa;
  border: 1px solid #d2dbdb;
  padding:  7px 45px;
  border-radius: 3px;
  outline: none;
  font-size: 14px;
  font-weight: 400;
}

.searchIcon{
  position: absolute;
  top: 10px;
  left: 30px;
  color: silver;
}

.navbar__last{
  width: 33.33333333333333%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.navbar__last li {
  list-style-type: none;
  display: inline-block;
  margin: 0 10px;
  cursor: pointer;
}

.navbar__icons{
  font-size: 25px;
  cursor: pointer;
}

.model {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left:0;
  background: rgba(0, 0, 0, 0.7);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}

.model__container{
  width: 400px;
  height: auto;
  padding :15px;
  border-radius: 4px;
  background: #fff;
  background: var(--white);
}

.model__group{
  margin: 10px 0;
}

.model__input{
  width: 100%;
  padding: 0 12px;
  height: 40px;
  border: 1px solid #d2dbdb;
  outline: none;
  border-radius: 30px;
}

.btn{
  outline: none;
  border: none;
  color: #fff;
  height: 40px;
  padding: 0 13px;
  border-radius: 3px;
  cursor: pointer;
}

.btn-smart{
  width:100%;
  background: #f83a63;
}

.model__group span {
  font-size: 13px;
  cursor: pointer;
}

.container{
  padding: 0 200 px;
  width: 100%;
  margin-top: 70px;
}

.create__second {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 10px;
}


.stories{
  /*display: flex;
  background: var(--white);
  padding:  15px 10px;
  width: 100%;
  border: 1px solid #efefef;
  border-radius: 3px;
  overflow: hidden;
  margin: 0 auto;*/
  width: 65%;
  margin: 15px 0;
  background: #fff;
  background: var(--white);
  padding: 10px;
  border: 1px solid #efefef;
  border-radius: 3px;
  overflow: hidden;
  padding:  15px 10px;
  display: -webkit-flex;
  display: flex;
}

.stories__info{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 6px;
  cursor: pointer;
}

.stories__img{
  width: 66px;
  height: 66px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #f83a63;
  padding: 3px;
}

.stories__img span {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
}

.stories__img span img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stories__name{
  font-size: 11px;
  margin-top: 4px;
}

.create{
  width: 65%;
  margin: 15px 0;
  background: #fff;
  background: var(--white);
  padding: 10px;
  border: 1px solid #efefef;
  border-radius: 3px;
}

.create__inputt{
  width: 100%;
  height: 40px;
  padding: 0 13px;
  border: 1px solid #efefef;
  border-radius: 2px;
  outline: none;
}

.file{
  display: none;
}

.camera{
  color: #f83a63;
  font-size: 30px;
  cursor: pointer;
}

.create__second-a{
  -webkit-flex:1 1;
          flex:1 1;
}

.create__second-b{
  -webkit-flex:1 1;
          flex:1 1;
  text-align: right;
}

.btn-sweet{
  background: #f83a63;
  color: #fff;
  color: var(--white);
  text-decoration: none;
  border: none;
  padding: 6px 10px;
  border-radius: 2px;
  cursor: pointer;
}

.posts{
  width: 65%;
  background: #fff;
  background: var(--white);
  margin: 15px 0;
}

.posts__header{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background: #fff;
  background: var(--white);
  padding: 10px;
}

.posts__header-avator{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: vr(--white);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #f83a63;
  text-transform: uppercase;
}

.post__header-name{
  margin: 8px;
  text-transform: capitalize;
}

.posts__img{
  width: 100%;
  overflow: hidden;
}

.posts__img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.comments{
  margin-top: 10px;
}

.comments__input{
  width: 100%;
  height: 40px;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
  border-left: none;
  border-right: none;
  padding: 0 15px;
  outline: none;
}

.comments__container{
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 10px 10px 10px;
}

.comments__container-name{
   margin-right: 8px;
   text-transform: capitalize;
}

.comments__container-img{
   font-size: 14px;
   color: silver;
   font-weight: 300;
}

.sidebar{
  position: fixed;
  top: 100px;
  right: 200px;
  width: 25%;
  padding-left:25px ;
  overflow: hidden;
}

.sidebar__user{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.sidebar__user-avator{
  width: 60px;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 18px;
  color: #fff;
  color: var(--white);
  background: #f83a63;
  border-radius: 50%;
  text-transform: uppercase;
  overflow: hidden;
}

.sidebar__user-name{
  padding-left: 10px;
  text-transform: capitalize;
}

.sidebar__list h3{
  font-size: 15px;
  font-weight: 400;
  color: silver;
}

.sidebar__list-user{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  margin-top: 10px;
}

.sidebar__user-a{
  -webkit-flex:1 1;
          flex:1 1;
  display: -webkit-flex;
  display: flex;
}

.sidebar__list-b{
  -webkit-flex:1 1;
          flex:1 1;
}

.sidebar__list-a-img{
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 50%;
}

.sidebar__list-a-name{
  margin-left: 8px;
  font-size: 14px;
}

.sidebar__list-b a{
  text-decoration: none;
  color: blue;
}

.sidebar__list-a-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.sidebar__user-img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
}

.sidebar__list-a{
  -webkit-flex:1 1;
          flex:1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.sidebar__list-b{
  -webkit-flex:1 1;
          flex:1 1;
  text-align: right;
  font-size: 14px;
}

@media (max-width: 765px){
  .navbar{
    padding: 0 20px;
  }
  .navbar__middle{
    display: none;
  }
  .navbar__icon{
    
    display: none;
  }
  .sidebar{
    display: none;
  }

  .sidebar__user{
    display: none;
  }

  .sidebar__list{
    display: none;
  }
  .container{
    padding: 0 240px;
  }
  .stories{
    width: 90%;
    margin: 0 auto;
  }
  .create{
    width: 90%;
    margin: 15px auto;
  }
  .posts{
    margin: 15px auto;
    width: 90%;
  }
}
